* {
    box-sizing: border-box;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.html-content-container {
    /* pointer-events: none; */
    /* position: absolute; */
    z-index: 1;
    /* width: 100%; */
    /* height: 100%; */
    /* display: grid; */
    /* place-content: center; */
}

ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
}

body {
